/* === Базовая настройка body === */
body {
  margin: 0;
  background: #121212; /* Тёмный фон */
  font-family: -apple-system, BlinkMacSystemFont, ...; /* Системные шрифты */
  color: #e0e0e0; /* Светлый текст */
  letter-spacing: 0.2px;
  line-height: 1.5;
}

/* === Основная обёртка компонента Otes === */
.otes-wrapper {
  padding: 0 40px;
  max-width: 1200px;
  margin: 0 auto;
  overflow: hidden;
  user-select: none;
  pointer-events: auto;
  -webkit-touch-callout: none;
}

/* Заблокировать выделение текста внутри всего Otes */
.otes-wrapper * {
  user-select: none !important;
  -webkit-touch-callout: none !important;
}

/* === Заголовок блока === */
.otes-title {
  text-align: left;
  font-size: 1.5rem;
  margin-bottom: 5px;
  color: #fff;
  letter-spacing: 1.5px;
  text-shadow: 0 0 10px rgba(255, 255, 255, 0.1);
}

/* === Сетка карточек === */
.otes-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  gap: 14px;
}

/* === Общая карточка === */
.otes-card {
  background: rgba(30, 30, 30, 0.4);
  backdrop-filter: blur(20px);
  border-radius: 16px;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.7), 0 0 1px rgba(255, 255, 255, 0.3);
  padding: 24px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  transition: transform 0.2s, box-shadow 0.2s;
}

/* === Заголовок карточки === */
.otes-card-title {
  display: flex;
  align-items: center;
  font-weight: bold;
  font-size: 1.1rem;
  margin-bottom: 8px;
  border-bottom: 1px solid #ffffff22;
  padding-bottom: 4px;
}

/* === Значение в карточке (напр. часы/сумма) === */
.otes-value {
  font-size: 1.8rem;
  font-weight: bold;
  margin-top: 4px;
}

/* === Цветовые классы === */
.otes-green, .otes-yellow, .otes-blue, .otes-red {
  color: inherit;
}
.otes-yellow { color: #FFD54F; }
.otes-blue { color: #4FC3F7; }
.otes-red { color: #EF5350; }

/* === Прогресс бар === */
.otes-progress {
  width: 100%;
  height: 10px;
  border-radius: 8px;
  background: rgba(255, 255, 255, 0.1);
  overflow: hidden;
  margin-top: 10px;
}

/* === Сам бегунок прогресса === */
.otes-progress-bar {
  height: 100%;
  background: #84e000;
  border-radius: 8px;
  transition: width 0.4s ease-in-out;
}

.otes-progress-bar.otes-yellow {
  background: linear-gradient(90deg, #facc15, #fde047);
}

.otes-progress-bar.otes-green {
  background: linear-gradient(90deg, #22c55e, #86efac);
}

.otes-progress-bar.otes-red {
  background: linear-gradient(90deg, #ef4444, #f87171);
}

/* === Кнопка обновления данных === */
.otes-update-button {
  margin: 40px auto 0;
  display: block;
  padding: 14px 24px;
  font-size: 1rem;
  background: #6FCF00;
  color: #121212;
  font-weight: bold;
  border: none;
  border-radius: 12px;
  cursor: pointer;
  transition: background 0.3s;
  box-shadow: 0 4px 20px rgba(111, 207, 0, 0.3);
}
.otes-update-button:hover {
  background: #84e000;
}

/* === Значок статуса выполнения цели === */
.otes-status-badge {
  margin-top: 12px;
  display: inline-flex;
  align-items: center;
  gap: 6px;
  font-size: 0.85rem;
  font-weight: bold;
  padding: 6px 10px;
  border-radius: 20px;
  background: rgba(255, 255, 255, 0.08);
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

/* === Серый бейдж если цель не задана === */
.otes-status-badge.otes-grey {
  background: rgba(255, 255, 255, 0.05);
  color: rgba(255, 255, 255, 0.4);
  font-style: italic;
  padding: 4px 10px;
  border-radius: 8px;
  font-size: 12px;
  margin-top: 8px;
}

/* === Анимация появления === */
.fade-in {
  animation: fadeIn 0.8s ease both;
}
@keyframes fadeIn {
  from { opacity: 0; transform: translateY(15px); }
  to   { opacity: 1; transform: translateY(0); }
}

/* === Цитата/подпись внизу === */
.otes-quote {
  text-align: center;
  margin-top: 50px;
  font-size: 1rem;
  color: rgba(255, 255, 255, 0.4);
  font-style: italic;
  letter-spacing: 0.5px;
}

/* === Сетка для инпутов === */
.otes-inputs {
  margin-bottom: 30px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 20px;
}

/* === Группа одного инпута с подписью === */
.otes-input-group {
  display: flex;
  flex-direction: column;
  gap: 6px;
  margin-top: 12px;
}

.otes-input-group label {
  font-size: 0.9rem;
  color: rgba(255, 255, 255, 0.7);
}

.otes-input-group input {
  padding: 10px;
  border-radius: 8px;
  background: rgba(255, 255, 255, 0.07);
  color: #fff;
  border: 1px solid rgba(255, 255, 255, 0.15);
  font-size: 1rem;
  outline: none;
  transition: 0.2s;
}
.otes-input-group input:focus {
  border-color: #6FCF00;
  background: rgba(255, 255, 255, 0.1);
}

/* === Диапазон дат и кнопки навигации === */
.otes-date-range {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-size: 14px;
  font-weight: 500;
}

.otes-date-left {
  margin: 0.5rem 0 1rem;
}

.otes-arrow-button {
  background: transparent;
  border: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  color: #fff;
  padding: 0;
}

.otes-nav-buttons {
  display: flex;
  gap: 8px;
}
.otes-nav-buttons button {
  background: none;
  color: #fff;
  font-size: 18px;
  border: none;
  cursor: pointer;
  padding: 4px 10px;
  border-radius: 8px;
  transition: 0.2s;
}

/* === Инпут-карточка === */
.otes-input-card .otes-inputs {
  margin-top: 20px;
}

/* === Текст под прогресс-баром === */
.otes-progress-info {
  margin-top: 10px;
  font-size: 14px;
}

/* === Единицы измерения рядом с цифрами === */
.unit {
  font-size: 0.75em;
  font-weight: normal;
  opacity: 0.8;
}

/* === Иконка в углу карточки === */
.otes-icon-corner {
  position: absolute;
  bottom: 8px;
  right: 8px;
  font-size: 28px;
  color: #ccc;
}

/* === Панель, которая слайдится (чаевые, проценты и т.п.) === */
.otes-slide-panel {
  max-height: 0;
  overflow: hidden;
  opacity: 0;
  transform: translateY(-10px);
  transition: all 0.4s ease;
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin-top: -10px;
}
.otes-slide-panel.open {
  max-height: 300px;
  opacity: 1;
  transform: translateY(0);
}

/* === Анимации слайдов влево/вправо === */
.animate-slide-left {
  animation: slideFromRight 0.5s ease forwards;
}
.animate-slide-right {
  animation: slideFromLeft 0.5s ease forwards;
}
@keyframes slideFromRight {
  0% { opacity: 0; transform: translateX(-100%); }
  100% { opacity: 1; transform: translateX(0); }
}
@keyframes slideFromLeft {
  0% { opacity: 0; transform: translateX(100%); }
  100% { opacity: 1; transform: translateX(0); }
}

/* === Кнопка "плюс" и выбор опции (чаевые/процент) === */
.otes-add-tip-btn {
  background: none;
  border: none;
  color: #fff;
  cursor: pointer;
  font-size: 20px;
  display: flex;
  align-items: center;
  gap: 6px;
}

.otes-plus-icon {
  font-size: 44px;
}
